import React from "react"
//import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import vista1 from "../images/V1_vista1.png"
import vista2 from "../images/V1_vista2.png"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    
    <h1>Bamboia V1</h1>
    <p>Un sistema capaz de entregar una elevada cantidad de pompas de jabón que además cuenta con varios modos, 
      uno lento que configura estructuras unidas de múltiples y grandes burbujas, 
      y otro más rápido que genera un mayor número de forma individual.</p>
    <img src={vista1} alt="Vista Bamboia V1" />
    <img src={vista2} alt="Vista Bamboia V1" />
    <h3>Características:</h3>
    <ul>
        <li>Partes impresas en 3d</li>
        <li>Ensamblaje modular</li>
        <li>Control vía Wifi (Placa ESP8266)</li>
        <li>ESPHOME Firmware</li>
        <li>Motor paso a paso principal Byj48</li>
        <li>Ventiladores GPU Gigabyte 80mm</li>
    </ul>
    <br>
    </br>
    <h3>Próximamente:</h3>
    <p>
      Archivos 3D STL disponibles en thingiverse. <br></br>
      Modelos diferentes de rotor.<br></br>
      Adaptador en eje del rotor para facilitar el intercambio de aros.<br></br>
      Mejor acceso y documentación del proyecto <br></br>
    </p>
    <br></br>
    <br></br>
    <h4 style={{ color: "darkred" }}>1r Test de página web <strong>(21/04/2020)</strong> (En construcción) .</h4>
  </Layout>
)
//<Link to="/page-2/">Go to page 2</Link>
/* style={{ margin: 0 }}>
<h1>Primera generación!</h1>

*/
export default IndexPage
